
@import "theme/blue.scss";
@import "nodeTerminalStyle.scss";

.node-input {
    position: relative;

    .input-required {
        margin-top: $margin-v * 0.25;
        margin-bottom: $margin-v * 0.25;
        color: $brand-danger;
        font-weight: 300;
        cursor: default;
    }
}

.input-terminal {
    @include node-terminal-style(left);

    &.input-active {
        &.can-accept {
            color: $brand-success;
        }

        &.can-not-accept {
            color: $brand-warning;
        }

        // expand size on drag
        &.is-dragging {
            --offset-extra: 10px;
        }

        &.mapped-over.is-dragging {
            --offset-extra: 5px;
        }

        &.is-dragover.can-accept::after {
            outline: solid 3px $brand-primary;
        }
    }
}

.delete-terminal-button {
    position: absolute;
    left: calc(-0.65rem - 5px);
    top: 50%;
    transform: translateY(-50%);
    display: grid;
    place-items: center;
    width: 0;
    height: 0;
    padding: 0;
    transition: none;
    border: none;
    background-color: $brand-danger;
    color: $white;

    .delete-button-icon {
        display: none;
        width: 14px;
        height: 14px;
    }

    &:hover,
    &:focus-visible,
    &:focus {
        background-color: $brand-danger;
        color: $white;
    }

    &:focus-visible,
    &:focus {
        box-shadow: 0 0 0 0.2rem $brand-primary;
    }

    .node-input:hover &,
    .node-input:focus-within &,
    .node-input.form-row-error & {
        width: 24px;
        height: 24px;

        .delete-button-icon {
            display: unset;
        }
    }
}
