
@import "theme/blue.scss";

.comment-color-selector {
    position: absolute;
    z-index: 10000;

    display: grid;
    grid-template-rows: 1rem 1rem;
    grid-template-columns: repeat(5, 1rem);
    grid-auto-flow: column;

    overflow: hidden;
    border-radius: 0.25rem;

    .color-button {
        --color: #{$brand-primary};
        --color-bright: #{$brand-secondary};

        background-color: var(--color);
        border-color: var(--color-bright);
        border-width: 0;
        border-radius: 0;
        padding: 0;

        width: 100%;
        height: 100%;

        display: grid;
        place-items: center;

        transition: none;

        &:hover {
            background-color: var(--color-bright);
        }

        &:focus,
        &:focus-visible {
            border-color: var(--color-bright);
            border-width: 2px;
            box-shadow: none;
        }

        &.selected::after {
            content: "";
            display: block;
            width: 50%;
            height: 50%;
            border-radius: 50%;
            background-color: var(--color-bright);
        }

        &.selected:hover::after {
            background-color: var(--color);
        }
    }
}
