
.workflow-canvas {
    position: relative;

    .canvas-content {
        width: 100%;
        position: relative;
        left: 0px;
        top: 0px;
        overflow: hidden;

        /* TODO: w/out this, canvas height = 0 when width goes beyond a point (invocation graph) */
        &.fixed-window-height {
            height: 60vh;
        }
    }

    .node-area {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        transform-origin: 0 0;
    }
}
