
@import "scss/theme/blue.scss";
.broadcast-card {
    border-radius: 0.25rem;
    border: 1px solid $gray-300;
    padding: 1rem;

    .expired-broadcast {
        text-decoration: line-through;
    }

    .published {
        color: $brand-primary;
    }

    .scheduled {
        color: $brand-warning;
    }

    .expires {
        color: $brand-info;
    }

    .expired {
        color: $brand-danger;
    }
}
