
@import "theme/blue.scss";

@function popper-border($border-color) {
    @return 1px solid $border-color;
}

.popper-element {
    z-index: 9999;
    border-radius: $border-radius-large;
}

/** Available variants */
.popper-element-dark {
    background: $brand-dark;
    border: popper-border($brand-dark);
    color: $brand-light;
    max-width: 12rem;
    opacity: 0.95;
    .popper-arrow:before {
        background: $brand-dark;
        border: popper-border($brand-dark);
    }
}

.popper-element-light {
    background: $white;
    border: popper-border($border-color);
    color: $brand-dark;
    .popper-arrow:before {
        background: $white;
        border: popper-border($border-color);
    }
}

.popper-element-primary-title {
    background: $white;
    border: popper-border($border-color);
    color: $brand-dark;
    .popper-header {
        background: $brand-primary;
        color: $white;
    }
    .popper-arrow:before {
        background: $brand-primary;
        border: popper-border($border-color);
    }
}

/** Arrow positioning and border handling */
.popper-arrow,
.popper-arrow:before {
    height: 9px;
    width: 9px;
    position: absolute;
    content: "";
    transform: rotate(45deg);
}

.popper-element[data-popper-placement^="top"] {
    > .popper-arrow {
        bottom: 0px;
    }
    > .popper-arrow:before {
        bottom: -5px;
        border-top: none;
        border-left: none;
    }
}

.popper-element[data-popper-placement^="right"] {
    > .popper-arrow {
        left: 0px;
    }
    > .popper-arrow:before {
        left: -5px;
        border-top: none;
        border-right: none;
    }
}

.popper-element[data-popper-placement^="bottom"] {
    > .popper-arrow {
        top: 0px;
    }
    > .popper-arrow:before {
        top: -5px;
        border-bottom: none;
        border-right: none;
    }
}

.popper-element[data-popper-placement^="left"] {
    > .popper-arrow {
        right: 0px;
    }
    > .popper-arrow:before {
        right: -5px;
        border-bottom: none;
        border-left: none;
    }
}
