
@import "theme/blue.scss";

$border-width: 6px;

.flex-panel {
    z-index: 100;
    flex-shrink: 0;
    display: flex;
    width: var(--width);
    position: relative;
    border-color: transparent;
    border-width: $border-width;
    box-shadow: 1px 0 transparent;
    transition: border-color 0.1s, box-shadow 0.1s;
    align-items: stretch;
    flex-direction: column;

    &::after {
        content: "";
        position: absolute;
        height: 100%;
        width: 1px;
        background-color: $border-color;
    }

    &.show-hover {
        border-color: $brand-info;

        &::after {
            background-color: $brand-info;
        }
    }

    &.left {
        border-right-style: solid;

        &::after {
            right: -1px;
        }

        .drag-handle {
            right: -$border-width;

            &:hover {
                right: calc(-1 * $border-width - var(--hover-expand) / 2);
            }
        }
    }

    &.right {
        border-left-style: solid;

        &::after {
            left: -1px;
        }

        .drag-handle {
            left: -$border-width;

            &:hover {
                left: calc(-1 * $border-width - var(--hover-expand) / 2);
            }
        }
    }
}

.drag-handle {
    background: none;
    border: none;
    border-radius: 0;
    position: absolute;
    width: $border-width;
    padding: 0;
    height: 100%;
    z-index: 10000;

    --hover-expand: 4px;

    &:hover {
        cursor: ew-resize;
        width: calc($border-width + var(--hover-expand));
    }
}

.collapse-button {
    position: absolute;
    z-index: 100;

    --width: 0px;

    width: var(--width);
    overflow: hidden;

    transition: width 0.1s, left 0.1s, right 0.1s;
    border-style: none;

    &:hover,
    &.show,
    &:focus {
        --width: 1.5rem;
        border-style: solid;
    }

    height: 4rem;
    top: calc(50% - 2rem);
    padding: 0;
    display: grid;
    place-items: center;

    &.left {
        right: calc(var(--width) * -1);
        border-top-left-radius: 0;
        border-bottom-left-radius: 0;
    }

    &.right {
        left: calc(var(--width) * -1);
        border-top-right-radius: 0;
        border-bottom-right-radius: 0;
    }

    &.closed {
        --width: 0.75rem;
        border-style: solid;

        > * {
            transform: translateX(-0.15rem);
        }

        &:hover,
        &:focus {
            --width: 1.5rem;
        }

        &.right {
            left: unset;
            right: 0;
        }

        &.left {
            right: unset;
            left: 0;
        }
    }
}

.interaction-overlay {
    height: 100%;
    position: fixed;
    left: 0;
    width: 100%;
    cursor: ew-resize;
}
