
@import "theme/blue.scss";
@import "buttonGroup.scss";

.frame-workflow-comment {
    width: 100%;
    height: 100%;

    .resize-container {
        z-index: 0;
    }

    &:focus-within {
        .resize-container {
            resize: both;
        }

        .color-selector {
            visibility: visible;
        }

        .style-buttons {
            visibility: visible;
        }
    }

    .style-buttons {
        visibility: hidden;
        @include button-group-style;
    }
}

.resize-container {
    --primary-color: #{$brand-primary};
    --secondary-color: #{$white};

    color: var(--primary-color);
    width: 100%;
    height: 100%;
    min-height: 100px;
    min-width: 100px;
    position: relative;
    overflow: hidden;

    border-radius: 0.25rem;
    border-color: var(--primary-color);
    border-style: solid;
    border-width: 2px;

    .frame-comment-header {
        background-color: var(--primary-color);
        color: $white;
        font-size: 1rem;
        padding: 0.1rem 0.5rem;
        position: relative;
        pointer-events: none;

        span {
            position: relative;
            pointer-events: all;
        }

        span:focus,
        span:focus-visible {
            border: none;
            outline: none;
            box-shadow: none;
        }
    }

    .draggable-pan {
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        cursor: move;
    }

    // coloring the "background" via ::after avoids zooming artifacts on the header
    display: flex;
    flex-direction: column;
    background-color: var(--primary-color);

    &::after {
        content: "";
        display: block;
        background-color: var(--secondary-color);
        flex: 1;
        position: relative;
    }

    &.multi-selected {
        box-shadow: 0 0 0 2px $white, 0 0 0 4px lighten($brand-info, 20%);
    }
}

.color-selector {
    visibility: hidden;
    right: 0;
    top: -4.5rem;
}
