
@import "theme/blue.scss";
@import "~scss/mixins.scss";

.plugin-list {
    display: flex;
    flex-direction: column;
    height: 100%;

    .plugin-list-items {
        overflow-y: auto;
    }

    .plugin-list-item {
        cursor: pointer;
        .plugin-list-image {
            padding: 1rem;
            width: 5rem;
            height: 4.3rem;
        }
        .plugin-list-icon {
            padding: 1rem;
            width: 5rem;
            height: 4.3rem;
            font-size: 2em;
            text-align: center;
            color: $text-color;
        }
        .plugin-list-text {
            word-break: break-word;
        }
    }
    .plugin-list-item:hover {
        .plugin-list-title {
            text-decoration: underline;
        }
        .plugin-list-text {
            text-decoration: underline;
        }
    }
}
