
@import "scss/theme/blue.scss";

.form-help {
    &:deep(h3) {
        font-size: $h4-font-size;
        font-weight: bold;
    }

    &:deep(h4) {
        font-size: $h5-font-size;
        font-weight: bold;
    }

    &:deep(h5) {
        font-size: $h6-font-size;
        font-weight: bold;
    }

    &:deep(h6) {
        font-size: $h6-font-size;
        text-decoration: underline;
    }
}
