
@import "scss/theme/blue.scss";

.heading {
    word-break: break-all;
}

.heading:deep(svg) {
    font-size: 0.75em;
}

// prettier-ignore
h1, h2, h3, h4, h5, h6 {
    &:not(.truncate) {
        display: flex;
    }
    align-items: center;
    gap: 0.4em;

    &.inline {
        display: inline-flex;
        margin-bottom: 0;
    }

    &.truncate {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
}

.collapsible {
    cursor: pointer;
}

.separator {
    display: grid;
    grid-template-columns: 1rem auto 1fr;
    gap: 1rem;
    align-items: center;
    margin-bottom: 0.5rem;

    // prettier-ignore
    h1, h2, h3, h4, h5, h6 {
        margin: 0;
    }

    .stripe {
        display: block;
        height: 1px;
        background-color: $brand-secondary;
    }
}
