
.grid-invocation {
    // prevent steps & zoom (within rowDetails) from overlapping header and footer
    &:deep(.grid-header) {
        z-index: 100;
    }
    &:deep(.grid-footer) {
        z-index: 100;
    }
    &:deep(.flex-panel.right) {
        z-index: 50;
    }
    &:deep(.zoom-control) {
        z-index: 50;
    }
}
