
@import "theme/blue.scss";

$margin: 1rem;

.broadcast-container {
    z-index: 999999;
    position: fixed;
    bottom: $margin;
    left: 50%;
    transform: translate(-50%, 0);
    height: 200px;
    width: min(calc(100% - $margin - $margin), 1200px);
    background: $white;
    border-color: $border-color;
    border-width: 1px;
    border-style: solid;
    border-radius: 0.5rem;
    display: grid;
    grid-template-columns: 50px 1fr 50px;
    grid-template-rows: 50px 1fr 50px;
    grid-template-areas:
        "i m x"
        "l m r"
        ". m .";

    .area-l {
        grid-area: l;
    }
    .area-m {
        grid-area: m;
    }
    .area-x {
        grid-area: x;
    }
    .area-r {
        grid-area: r;
    }
    .area-i {
        grid-area: i;
    }

    &.preview {
        position: relative;
        bottom: 0;
    }

    &.warning {
        border-color: $brand-warning;
    }

    &.urgent {
        border-color: $brand-danger;
        background: linear-gradient(to top, lighten($brand-warning, 35%), $white 120px);
    }

    .arrow {
        font-size: 2rem;

        &.left {
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
        }

        &.right {
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
        }
    }

    .dismiss-button {
        font-size: 1.5rem;
        color: $border-color;

        &:hover {
            background: none;
            color: $brand-danger;
        }

        &:focus {
            color: $brand-primary;
        }
    }

    .info-icon {
        display: grid;
        place-items: center;
        font-size: 1.5rem;

        .urgent {
            color: $brand-danger;
        }

        .warning {
            color: $brand-warning;
        }
    }

    .main-content {
        display: flex;
        flex-direction: column;
        height: 100%;
        padding: 0.75rem 0.25rem;

        .message {
            flex: 1;
            overflow-y: scroll;
        }

        .bottom-row {
            display: grid;
            grid-template-columns: 1fr auto 1fr;

            .action-links {
                display: flex;
                flex-wrap: wrap;
                gap: 0.5rem;
            }

            .page-indicator {
                align-self: end;
                justify-self: center;
            }
        }
    }
}
