
@import "theme/blue.scss";

.nav-item {
    display: flex;
    align-items: center;
    align-content: center;
    justify-content: center;
}

.nav-icon {
    @extend .nav-item;
    font-size: 1rem;
}

.indicator {
    position: absolute;
    top: -0.2rem;
    right: -0.1rem;
    width: 0.6rem;
    height: 0.6rem;
    border-radius: 50%;
    background: $brand-danger;
}
