
.notification-actions {
    @container notification-content (max-width: 576px) {
        grid-row: 3;
    }

    .notification-actions-body {
        display: flex;
        gap: 0.5rem;
        align-items: center;
        justify-content: space-between;

        .notification-actions-buttons {
            gap: 0.5rem;
        }
    }
}
