
.chart-description {
    text-align: center;
    margin-bottom: 20px;
}

.chart-area {
    display: flex;
    justify-content: center;
}

.bar-chart {
    float: left;

    &:deep(svg) {
        overflow: visible;
    }

    &:deep(.bar.clickable) {
        cursor: pointer;
    }
}

.legend {
    float: right;
    height: 400px;
    overflow: auto;

    &:deep(.legend-item) {
        font-size: 14px;

        &.clickable {
            cursor: pointer;

            &:hover {
                text-decoration: underline;
            }
        }
    }
}

.chart-tooltip {
    position: fixed;
    background-color: #fff;
    border: 1px solid #000;
    border-radius: 5px;
    padding: 5px;
    margin: 0 0 0 20px;
    z-index: 100;
    text-align: center;
    pointer-events: none;
}

.selection-info {
    background-color: #fff;
    border: 1px solid #000;
    border-radius: 5px;
    padding: 5px;
    z-index: 100;
    display: block;
    float: right;
    position: absolute;
    bottom: 0;
    right: 0;
    margin-bottom: 2rem;
    margin-right: 2rem;
    text-align: left;
    max-width: 300px;
}
