
@import "theme/blue.scss";
@import "nodeTerminalStyle.scss";

.node-output-buttons {
    overflow-wrap: anywhere;
}
.node-output {
    display: flex;
    position: relative;
}

.node-output-buttons {
    display: flex;
    flex-direction: row;
    margin-left: -0.2rem;
}

.output-terminal {
    @include node-terminal-style(right);

    &:not(.blank-output) {
        &:hover {
            color: $brand-success;
        }

        button:focus + .terminal-icon {
            color: $brand-success;
        }
    }
}

.connection-menu-button {
    border: none;
    position: absolute;
    width: 0;
    height: 0;
    padding: 0;
    transition: none;

    &:focus,
    &:focus-visible {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        box-shadow: 0 0 0 0.2rem $brand-primary;
        background-color: $white;
    }
}
