
@import "scss/theme/blue.scss";

.headless-multiselect {
    fieldset {
        display: flex;
        flex-direction: row;
        height: 1.75rem;
        align-items: center;

        input {
            text-decoration: none;
            background: none;
            border: none;
            padding-left: 0.25rem;
            flex-grow: 1;
            margin: 0;
            border: none;

            &:focus,
            &:focus-visible {
                outline: none;
                border: none;
            }
        }

        button {
            background: none;
            border: none;
            padding: 0;
            width: 2rem;
        }
    }

    .toggle-button {
        font-size: $font-size-base;
        color: $text-color;
        text-decoration: none;
        padding: 0 0.25rem;
        background: none;
        cursor: text;
        text-align: left;
        margin: 0;
        border: none;
        width: 100%;
        height: 1.75rem;
    }
}

.headless-multiselect__options {
    position: fixed;
    z-index: 10000;
    overflow-y: scroll;

    top: 0;
    left: 0;
    transform: translate(var(--left), calc(var(--top) + var(--height)));

    width: var(--width);
    max-height: min(300px, calc(100% - var(--top) - var(--height) - 12px));

    display: flex;
    flex-direction: column;
    box-shadow: 0 0 6px 0 rgba(3, 0, 34, 0.048), 0 0 4px 0 rgba(3, 0, 34, 0.185);
    border-bottom-left-radius: 4px;
    border-bottom-right-radius: 4px;

    background-color: $white;

    .headless-multiselect__option {
        padding: 0.4rem 0.5rem;
        display: inline-block;
        text-align: start;
        background-color: $white;
        color: $brand-primary;
        border: none;
        border-radius: 0;
        font-weight: bold;
        transition: none;

        &:focus,
        &:focus-visible {
            border: none;
            outline: none;
            box-shadow: none;
        }

        &.invalid {
            color: $brand-light;
            background-color: $brand-warning;
        }

        .headless-multiselect__info {
            float: right;
        }

        .headless-multiselect__needs-highlight {
            visibility: hidden;
        }

        &.highlighted {
            background-color: $brand-primary;
            color: $white;

            .headless-multiselect__needs-highlight {
                visibility: unset;
            }
        }

        &[aria-selected].highlighted {
            background-color: $brand-danger;
        }
    }
}
