
@import "theme/blue.scss";

.workflow-published {
    display: flex;
    height: 100%;

    .container-root {
        container-type: inline-size;
        width: 100%;
        overflow: auto;
    }

    .published-workflow {
        display: grid;
        gap: 0.5rem 1rem;
        grid-template-rows: max-content;
        grid-template-columns: auto auto 30%;

        height: 100%;

        .workflow-header {
            grid-column: 1 / span 3;

            display: flex;
            justify-content: end;
        }

        .workflow-preview {
            grid-column: 1 / span 2;

            &.only-preview {
                grid-column: 1 / span 3;
            }
        }

        &:deep(.workflow-information-container) {
            height: 100%;
            max-width: 500px;
            overflow: auto;
        }
    }

    @container (max-width: 900px) {
        .published-workflow {
            height: unset;
            grid-template-columns: auto;

            .workflow-preview {
                grid-column: 1 / span 3;
                height: 450px;
            }

            &:deep(.workflow-information-container) {
                max-width: unset;
                height: unset;
            }

            .workflow-information-container {
                grid-column: 1 / span 3;
            }
        }
    }

    .view-button {
        --fa-secondary-color: #{$brand-toggle};
        --fa-secondary-opacity: 1;
    }
}
