
@import "scss/theme/blue.scss";

button {
    padding: 0.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    gap: 0.5rem;
    border-radius: 12px;
    border: 1px solid $brand-secondary;
}

button:hover {
    background-color: $brand-secondary;
}

.value {
    display: inline-flex;
    align-items: center;
    gap: 1rem;
}

#heading {
    font-size: 1rem;
}

#total-emissions {
    font-size: 1.25rem;
}
