
@import "scss/theme/blue.scss";
.list-container {
    .history-picker {
        min-width: 15rem;
        max-width: 15rem;
        margin: 1rem;
        .history-picker-box {
            border: dotted lightgray;
            cursor: pointer;
            position: relative;
            justify-content: center;
            display: flex;
            align-items: center;
            text-wrap: none;
            &.top-picker {
                height: 20%;
            }
            &.bottom-picker {
                height: 80%;
            }
            &:not(.history-picker-drop-zone) {
                &:hover {
                    background-color: rgba($brand-info, 0.2);
                }
            }
            &.history-picker-drop-zone {
                height: 100%;
            }
        }
    }

    position: relative;

    &:before,
    &:after {
        position: absolute;
        content: "";
        pointer-events: none;
        z-index: 10;
        width: 20px;
        height: 100%;
        top: 0;
        opacity: 0;

        background-repeat: no-repeat;
        transition: opacity 0.4s;
    }

    &:before {
        left: 0;
        background-image: linear-gradient(to right, rgba(3, 0, 48, 0.1), rgba(3, 0, 48, 0.02), rgba(3, 0, 48, 0));
    }

    &:not(.scrolled-left) {
        &:before {
            opacity: 1;
        }
    }

    &:after {
        right: 0;
        background-image: linear-gradient(to left, rgba(3, 0, 48, 0.1), rgba(3, 0, 48, 0.02), rgba(3, 0, 48, 0));
    }

    &:not(.scrolled-right) {
        &:after {
            opacity: 1;
        }
    }
}
