
@import "theme/blue.scss";
@import "buttonGroup.scss";

$gap-x: 0.8rem;
$gap-y: 0.5rem;

$min-width: 1.5em;
$min-height: 1.5em;

.markdown-workflow-comment {
    position: absolute;
    width: 100%;
    height: 100%;

    .resize-container {
        z-index: 50;
    }

    &:focus-within {
        .resize-container {
            resize: both;
        }

        .style-buttons {
            visibility: visible;
        }

        .color-selector {
            visibility: visible;
        }

        .rendered-markdown {
            visibility: hidden;
        }

        .markdown-textarea {
            width: calc(100% - $gap-x - $gap-x);
            height: calc(100% - $gap-y - $gap-y);
        }
    }

    label {
        margin: 0;
        padding: 0;
        height: 100%;
        position: absolute;
        top: 0;
    }

    .markdown-textarea {
        border: none;
        background-color: transparent;
        padding: 0;
        margin: 0;
        resize: none;

        position: absolute;
        top: $gap-y;
        left: $gap-x;

        line-height: 1.2;

        &:focus-visible {
            box-shadow: none;
            border: none;
            outline: none;
        }

        width: 0;
        height: 0;
    }

    .style-buttons {
        visibility: hidden;
        @include button-group-style;
    }
}

.rendered-markdown {
    position: absolute;
    top: $gap-y;
    left: $gap-x;
    overflow-y: auto;
    line-height: 1.2;

    width: calc(100% - $gap-x - $gap-x);
    max-height: calc(100% - $gap-y - $gap-y);

    min-width: $min-width;
    min-height: $min-height;

    &:deep(h2),
    &:deep(h3),
    &:deep(h4),
    &:deep(h5),
    &:deep(h6) {
        margin-bottom: 0.2em;
    }

    &:deep(h2) {
        font-size: $h1-font-size;
    }

    &:deep(h3) {
        font-size: $h2-font-size;
    }

    &:deep(h4) {
        font-size: $h3-font-size;
    }

    &:deep(h5) {
        font-size: $h4-font-size;
    }

    &:deep(h6) {
        font-size: $h5-font-size;
    }

    &:deep(ul),
    &:deep(ol) {
        margin-bottom: 0.5rem;
        padding-left: 1.5rem;
    }

    &:deep(p) {
        margin-bottom: 0.5rem;
    }

    &:deep(blockquote) {
        padding-left: 0.5rem;
        border-left: 2px solid var(--primary-color);
        margin-bottom: 0.5rem;

        p {
            margin-bottom: 0;
        }
    }

    &:deep(a) {
        text-decoration: underline;
    }
}

.resize-container {
    --primary-color: #{$brand-primary};

    color: var(--primary-color);
    background-color: $white;

    .markdown-textarea {
        color: var(--primary-color);
    }

    width: 100%;
    height: 100%;
    min-width: calc($min-width + $gap-x + $gap-x);
    min-height: calc($min-height + $gap-y + $gap-y);
    position: relative;
    overflow: hidden;

    border-radius: 0.25rem;
    border-color: var(--primary-color);
    border-style: solid;
    border-width: 2px;

    .draggable-pan {
        cursor: move;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
    }

    &.multi-selected {
        box-shadow: 0 0 0 2px $white, 0 0 0 4px lighten($brand-info, 20%);
    }
}

.color-selector {
    visibility: hidden;
    right: 0;
    top: -4.5rem;
}
