
@import "theme/blue.scss";
.upload-settings-content {
    position: relative;
    .upload-settings-cover {
        background: $white;
        cursor: no-drop;
        height: 100%;
        left: 0;
        opacity: 0.25;
        position: absolute;
        top: 0;
        width: 100%;
    }
    .upload-settings-table {
        tr {
            cursor: pointer;
        }
        tr:hover {
            background-color: lighten($brand-success, 20%);
        }
    }
}
