
@import "theme/blue.scss";

.activity-panel {
    height: 100%;
    display: flex;
    flex-flow: column;
    padding: 0.5rem 1rem;
    background-color: $brand-light;

    .activity-panel-header {
        padding-bottom: 0.5rem;

        .activity-panel-header-top {
            display: flex;
            align-items: center;
            justify-content: space-between;
            min-height: 2rem;

            .activity-panel-heading {
                margin: 0;
            }
        }
    }

    .activity-panel-body {
        display: flex;
        flex-direction: column;
        flex-grow: 1;
        overflow-y: hidden;
        button:first-child {
            background: none;
            border: none;
            text-align: left;
            transition: none;
            width: 100%;
            border-color: transparent;
        }
        button:first-child:hover {
            background: $gray-200;
        }
    }

    .activity-panel-footer {
        margin-top: 0.5rem;
        width: 100%;
        font-weight: bold;
    }
}
