
@import "theme/blue.scss";

.box-select-preview {
    pointer-events: none;
    position: absolute;
    width: var(--width);
    height: var(--height);
    top: var(--position-top);
    left: var(--position-left);

    border: dashed 2px $brand-info;
    background-color: rgba($brand-info, 0.25);

    &.remove-mode {
        border-color: $brand-danger;
        background-color: rgba($brand-danger, 0.25);
    }
}
