
@import "theme/blue.scss";

.form-select-many {
    .search-bar {
        display: grid;
        grid-template-columns: 1fr 2.125rem 2.125rem;

        > *:not(:first-child):not(:last-child) {
            border-radius: 0;
        }

        > *:last-child {
            border-top-left-radius: 0;
            border-bottom-left-radius: 0;
        }

        .search-input {
            position: relative;

            input {
                border-top-right-radius: 0;
                border-bottom-right-radius: 0;
            }

            button {
                position: absolute;
                right: 4px;
                top: 4px;
                height: calc(100% - 8px);
                width: 26px;

                &.hidden:not(:focus) {
                    color: transparent;

                    &:hover {
                        color: $white;
                    }
                }
            }
        }
    }

    .toggle-button {
        padding-left: 0;
        padding-right: 0;
    }
}

.options-box {
    resize: vertical;
    overflow: hidden;
    min-height: 200px;
    height: 200px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto 1fr;
    grid-auto-flow: column;
    padding: 0.5rem;

    .selection-heading {
        color: $gray-600;
        font-weight: bold;
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;

        .selection-button {
            height: 20px;
            padding: 0 0.5rem;
            display: flex;
            align-items: center;
            gap: 0.25rem;
        }
    }
}

.options-list {
    overflow-y: scroll;
    display: flex;
    flex-direction: column;

    button {
        text-align: start;
        border: none;
        padding: 0 0.5rem;
        background: none;
        transition: none;
        display: flex;
        justify-content: space-between;

        &.highlighted {
            background-color: $brand-info;
            border-radius: 0;
            color: $white;

            &:hover,
            &:focus {
                background-color: $brand-primary;

                &::after {
                    color: $white;
                }
            }
        }

        &:focus-visible {
            outline-color: $brand-primary;
            outline-width: 2px;
            outline-offset: -2px;
            outline-style: solid;
            box-shadow: none;
        }

        &:hover,
        &:focus {
            background-color: $brand-secondary;

            &::after {
                content: "click to select";
                color: darken($brand-secondary, 40%);
                font-weight: bold;
            }
        }

        &:focus-visible::after {
            content: "enter to select";
        }
    }

    .show-more-indicator {
        display: flex;
        font-style: italic;
        padding-left: 0.5rem;
        color: darken($gray-400, 20%);

        .show-more-button:hover::after,
        .show-more-button:focus::after {
            content: none;
        }

        .show-more-button {
            color: $brand-info;
            text-decoration: underline;
        }
    }
}

.options-list.selected {
    button {
        &:hover,
        &:focus {
            &::after {
                content: "click to deselect";
            }
        }

        &:focus-visible::after {
            content: "enter to deselect";
        }
    }
}

.bottom-row-info {
    font-style: italic;
    color: darken($gray-400, 10%);
    font-size: 0.75rem;
    padding: 0 0.25rem;
    width: 100%;
    display: flex;
    justify-content: space-between;

    .working-indicator {
        color: $brand-primary;
    }
}
