
.quote-container {
    position: relative;
    height: 100%;
    margin-right: 0.5rem;
}
.quota-text {
    @extend .quote-container;
    background: var(--masthead-link-color);
    padding-right: 0.5rem;
    padding-left: 0.5rem;
    a {
        color: var(--masthead-text-color);
        text-decoration: none;
    }
}
.quota-meter {
    @extend .quote-container;
    .quota-progress {
        width: 100px;
        height: 1.4em;
        position: relative;
        & > * {
            position: absolute;
            width: 100%;
            height: 100%;
            text-align: center;
        }
        & > span {
            line-height: 1.4em;
            pointer-events: none;
        }
    }
    :deep(a) {
        &:focus-visible {
            outline: 2px solid var(--masthead-text-hover);
        }
    }
}
