
@import "scss/mixins.scss";
@import "breakpoints.scss";

.workflows-list {
    overflow: auto;
    display: flex;
    flex-direction: column;

    .workflow-total {
        display: grid;
        text-align: center;
        margin-top: 1rem;
    }

    .workflows-list-header {
        top: 0;
        z-index: 100;
    }

    .cards-list {
        container: card-list / inline-size;
        scroll-behavior: smooth;
        min-height: 150px;

        overflow-y: auto;
        overflow-x: hidden;

        .list-view {
            width: 100%;
        }

        .grid-view {
            width: calc(100% / 3);
        }

        @container card-list (max-width: #{$breakpoint-xl}) {
            .grid-view {
                width: calc(100% / 2);
            }
        }

        @container card-list (max-width: #{$breakpoint-sm}) {
            .grid-view {
                width: 100%;
            }
        }
    }
}
