
@import "theme/blue.scss";

.freehand-workflow-comment {
    --color: #{$brand-primary};
    --thickness: 5px;

    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1600;
    overflow: visible;

    fill: none;
    stroke-linecap: round;

    path {
        stroke-width: var(--thickness);
        stroke: var(--color);
    }

    pointer-events: none;

    &.multi-selected {
        border-radius: 0.25rem;
        box-shadow: 0 0 0 2px $white, 0 0 0 4px lighten($brand-info, 20%);
    }
}
