
@import "theme/blue.scss";

.workflow-embed {
    display: flex;
    gap: 0.5rem;
}

@container (max-width: 1200px) {
    .workflow-embed {
        flex-direction: column;
    }
}

.workflow-embed {
    .settings {
        flex: 1;
        display: flex;
        align-items: start;
        justify-content: start;
        flex-direction: column;
    }

    .preview {
        flex: 1;

        .published-preview {
            border: 2px solid $brand-primary;
            border-radius: 4px;
            width: 100%;
            height: 550px;
        }

        .embed-code-input {
            background-color: transparent;
        }
    }
}

.control-label {
    display: flex;
    flex-direction: column;
    margin-top: 0.25rem;
}

.zoom-control,
.position-control {
    position: unset;
    padding: 2px 4px;
    border-color: $brand-primary;
    border-radius: 4px;
    border-width: 1px;
    border-style: solid;
}

.position-control {
    display: flex;
    flex-direction: column;
    width: 9rem;

    input {
        width: 100%;
    }

    label {
        margin-bottom: 0;
        display: flex;
        gap: 0.25rem;
        align-items: center;
    }
}
