
@import "theme/blue.scss";
@import "base.scss";

.card-body {
    overflow: auto;
}

table {
    td,
    th {
        text-align: left;
        padding: 5px;
        line-height: $line-height-base;
    }
    th {
        background-color: $table-heading-bg;
    }
    tr:nth-child(even) {
        background-color: $table-bg-accent;
    }
}
