
.notifications-preferences {
    .notifications-preferences-header {
        flex-grow: 1;
    }

    .notifications-preferences-description {
        margin-bottom: 1rem;
    }

    .notifications-preferences-body {
        display: flex;
        justify-content: space-around;

        .category-header {
            gap: 1rem;
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 0.5rem;
        }

        .category-channel {
            display: flex;
            align-items: center;
        }
    }

    .push-notifications-notice {
        margin: 0.5rem auto;
        width: fit-content;
    }
}

.category-title {
    font-weight: bold;
}

.category-description {
    font-size: 0.8rem;
    font-style: italic;
    margin-bottom: 0.5rem;
}

.card-container {
    margin: 0.5rem;
}
