
@import "theme/blue.scss";
@import "buttonGroup.scss";

.text-workflow-comment {
    position: absolute;
    width: 100%;
    height: 100%;

    .resize-container {
        z-index: 200;
    }

    &:focus-within {
        .style-buttons {
            visibility: visible;
        }

        .color-selector {
            visibility: visible;
        }

        .resize-container {
            resize: both;
            border-color: $brand-primary;
        }
    }

    .style-buttons {
        visibility: hidden;
        @include button-group-style;
    }
}

.resize-container {
    --font-size: 1rem;
    --font-color: #{$brand-primary};

    color: var(--font-color);
    font-size: var(--font-size);

    width: 100%;
    height: 100%;

    min-height: calc(1em + 10px);
    min-width: calc(1em + 20px);

    border-color: transparent;
    border-radius: 0.25rem;
    border-width: 2px;
    border-style: solid;

    overflow: hidden;

    position: absolute;

    span {
        position: absolute;
        margin: 5px 10px;
        line-height: 1;

        &.bold {
            font-weight: 700;
        }

        &.italic {
            font-style: italic;
        }

        &:focus,
        &:focus-visible {
            border: none;
            outline: none;
        }
    }

    .draggable-pan {
        cursor: move;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
    }

    &.resizable:focus-within,
    &.resizable:focus {
        resize: both;
        border-color: $brand-primary;
    }

    &.multi-selected {
        box-shadow: 0 0 0 2px $white, 0 0 0 4px lighten($brand-info, 20%);
    }
}

.color-selector {
    visibility: hidden;
    right: 0.75rem;
    top: -4.5rem;
}
