
@import "theme/blue.scss";

.activity-settings {
    overflow-y: hidden;
    display: flex;
    flex-direction: column;
}

.activity-settings-content {
    overflow-y: auto;
}

.activity-settings-item {
    background: none;
    border: none;
    text-align: left;
    transition: none;
    width: 100%;

    .icon-check {
        color: darken($brand-success, 15%);
    }
    .button-delete {
        background: transparent;
    }
}
.activity-settings-item:hover {
    background: $gray-200;
}
